@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background: linear-gradient(0.25turn, 
    rgba(27,79,211,0.6) 25%, 
    rgba(10,53,165,0.6) 66%, 
    rgba(1,17,57,0.6) 100%),
    url(/battleship/static/media/submarine.f1055bbd.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  font-size: 10px;
}

.App {
  width: 95vw;
  height: 95vh;
  margin: 0 auto;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.start-over-container {
  position: fixed;
  top: 3rem;
  left: 3rem;
}

.start-over-btn {
  font-family: 'Black Ops One', cursive;
  width: 12rem;
  height: 4.5rem;
  font-size: 1.5rem;
  background: rgb(78, 78, 78);
  color: white;
}

.start-over-btn:hover {
  background: rgb(104, 104, 104);
}

.start-over-btn:active {
  background: rgb(71, 71, 71);
}

/* MOBILE */

@media (max-width: 900px) {
  .start-over-btn {
    width: 10rem;
    height: 3rem;
    font-size: 1.2rem;
  }
}


.set-players-container {
  width: auto;
  height: auto;
}

#welcome-header-container {
  display: block;
  width: 50rem;
  height: auto;
  margin: 0 auto 3rem auto;
  text-align: center;
}

#welcome-header {
  display: block;
  margin: 0 auto;
  text-align: center;
  font-size: 6rem;
  color: white;
  font-family: 'Black Ops One', cursive;
}

.set-players-btn {
  width: 25rem;
  height: 12rem;
  font-size: 4rem;
  color: red;
  text-shadow: 1px 1px black;
  font-family: 'Black Ops One', cursive;
  border: 2px groove black;
  background: rgb(151, 151, 151);
}

.set-players-btn:hover {
  background: rgb(105, 105, 105);
  color: rgb(255, 31, 31);
}

.set-players-btn:active {
  background: rgb(73, 73, 73);
  color: rgb(248, 70, 70);
}

/* MOBILE */

@media (max-width: 900px) {
  .set-players-container {
    width: auto;
    height: auto;
  }
  
  #welcome-header-container {
    display: block;
    width: auto;
    height: auto;
    margin: 0 auto 3rem auto;
    text-align: center;
  }
  
  #welcome-header {
    display: block;
    margin: 0 auto;
    text-align: center;
    font-size: 5rem;
    color: white;
    font-family: 'Black Ops One', cursive;
  }
  
  .set-players-btn {
    width: 25rem;
    height: 12rem;
    font-size: 4rem;
    color: red;
    text-shadow: 1px 1px black;
    font-family: 'Black Ops One', cursive;
    border: 2px groove black;
    background: rgb(151, 151, 151);
  }
  
  .set-players-btn:hover {
    background: rgb(105, 105, 105);
    color: rgb(255, 31, 31);
  }
  
  .set-players-btn:active {
    background: rgb(73, 73, 73);
    color: rgb(248, 70, 70);
  }
}
#player-form-container {
  display: block;
  margin: 0 auto;
  width: auto;
  height: auto;
  text-align: center;
}

.player-form {
  font-family: 'Black Ops One', cursive;
}

.name-label {
  font-size: 3rem;
  color: white;
  text-shadow: 1px 1px black;
  display: block;
  width: auto;
}

.name-input {
  font-size: 2rem;
  display: block;
  margin: 2rem auto;
  font-family: 'Black Ops One', cursive;
  font-weight: 100;
  font-style: normal;
}

.submit-form-btn {
  width: auto;
  height: auto;
  padding: 1rem 2rem;
  background: rgba(255, 0, 0, 0.6);
  color: white;
  margin: 3rem auto;
  font-size: 1.25rem;
  font-family: 'Black Ops One', cursive;
}

.submit-form-btn:hover {
  background: rgba(255, 44, 44, 0.6);
}

.start-btn-container {
  display: block;
  margin: 0 auto;
  width: auto;
  height: auto;
  text-align: center;
}

.start-btn {
  font-family: 'Black Ops One', cursive;
  background: rgb(0, 0, 0, 0.6);
  color: red;
  width: 18rem;
  height: 7rem;
  font-size: 2rem;
}

.start-btn:hover {
  background: rgba(32, 32, 32, 0.6);
}

/* MOBILE */

@media (max-width: 900px) {
  #player-form-container {
    display: block;
    margin: 0 auto;
    width: auto;
    height: auto;
    text-align: center;
  }
  
  .player-form {
    font-family: 'Black Ops One', cursive;
  }
  
  .name-label {
    font-size: 3rem;
    color: white;
    text-shadow: 1px 1px black;
    display: block;
    width: auto;
  }
  
  .name-input {
    font-size: 2rem;
    display: block;
    margin: 2rem auto;
    font-family: 'Black Ops One', cursive;
    font-weight: 100;
    font-style: normal;
  }
  
  .submit-form-btn {
    width: auto;
    height: auto;
    padding: 1rem 2rem;
    background: rgba(255, 0, 0, 0.6);
    color: white;
    margin: 3rem auto;
    font-size: 2rem;
    font-family: 'Black Ops One', cursive;
  }
  
  .start-btn-container {
    display: block;
    margin: 0 auto;
    width: auto;
    height: auto;
    text-align: center;
  }
  
  .start-btn {
    font-family: 'Black Ops One', cursive;
    background: rgb(0, 0, 0, 0.6);
    color: red;
    width: 18rem;
    height: 7rem;
    font-size: 2rem;
  }
}
.tile {
  width: 3rem;
  height: 3rem;
  border: 2px solid black;
  background-color: rgb(123,199,247);
  margin: 0;
}

/* DYNAMIC CLASSES */

.ship-preview,
.current-ship {
  width: 3rem;
  height: 3rem;
  border: 2px solid black;
  background-color: rgba(123,199,247, 0.8);
}

.blank-tile {
  width: 3rem;
  height: 3rem;
  border: 2px solid black;
}

.preview {
  width: 3rem;
  height: 3rem;
  border: 2px solid black;
  background-color: rgba(150,18,6, 0.3);
  z-index: 2;
}

.hover {
  background-color: rgba(150,18,6, 0.3);
  z-index: 2;
}

.current-ship,
.own-ship {
  background-color: rgb(150,18,6);
}

.hit {
  background-color: black;
}

.splash {
  background-color: white;
}
.letter {
  margin: 0 auto;
}
.number {
  margin: 0 auto;
  padding: 0.25rem;
  font-size: 2.3rem;
}
.tile-display {
  margin: 0 auto;
  width: 48rem;
  height: 50rem;
  display: inline-block;
}

.target-header {
  font-family: 'Black Ops One', cursive;
  color: red;
}

.enemy {
  font-family: 'Black Ops One', cursive;
  color: rgb(204, 13, 13);
}

.numbers {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  font-size: 1.5rem;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 5rem;
  height: 31rem;
  position: absolute;
  font-family: 'Black Ops One', cursive;
}

.tiles {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  flex: 1 1;
  align-items: center;
  justify-content: center;
  width: 40rem;
  height: 31rem;
  margin: 0 auto;
}

.letters {
  display: flex;
  flex: 1 1;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 38rem;
  height: 5rem;
  font-size: 1.5rem;
  margin: 0 auto;
  position: absolute;
  left: 47px;
  font-family: 'Black Ops One', cursive;
  position: relative;
  left: 0;
}

/* MOBILE */

@media (max-width: 900px) {
  .tile-display {
    margin: 0 auto;
    width: 48rem;
    height: 50rem;
    display: block;
  }
  
  .target-header {
    font-family: 'Black Ops One', cursive;
    color: red;
  }
  
  .enemy {
    font-family: 'Black Ops One', cursive;
    color: rgb(204, 13, 13);
  }
  
  .numbers {
    display: flex;
