    flex-wrap: wrap;
    width: 5rem;
    height: 31rem;
    position: absolute;
    font-family: 'Black Ops One', cursive;
  }
  
  .tiles {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    flex: 1 1;
    align-items: center;
    justify-content: center;
    width: 40rem;
    height: 31rem;
    margin: 0 auto;
  }
  
  .letters {
    display: flex;
    flex: 1 1;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 38rem;
    height: 5rem;
    font-size: 1.5rem;
    margin: 0 auto;
    position: absolute;
    left: 47px;
    font-family: 'Black Ops One', cursive;
    position: relative;
    left: 0;
  }
}

.winner-header-container {
  display: block;
  margin: 0 auto;
  width: auto;
  height: auto;
}

#winner-header {
  font-size: 4rem;
  color: white;
  font-weight: 400;
  font-family: 'Black Ops One', cursive;
}

/* MOBILE */

@media (max-width: 900px) {
  .winner-header-container {
    display: block;
    margin: -35rem auto;
    width: auto;
    height: auto;
  }
  
  #winner-header {
    font-size: 3.25rem;
    color: white;
    font-weight: 400;
    font-family: 'Black Ops One', cursive;
  }
}
.loading-container {}

#loading-header {
  font-family: 'Black Ops One', cursive;
  color: white;
}
@keyframes fadeout {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    transform: scale(1.2);
  }
}

.game-container {
  font-family: 'Black Ops One', cursive;
  color: white;
  display: block;
  margin: 10rem auto 0 auto;
  height: auto;
  width: 100%;
}

#hit-result-text {
  font-size: 8rem;
  color: red;
  font-family: 'Black Ops One', cursive;
  margin: 0 auto;
  display: block;
}

.animated-text {
  animation: fadeout 1s;
}

.tile-container { /* Contains both the enemy map and the personal map */
  display: flex;
  flex: 1 1;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: auto;
  margin: 0 auto;
}

.legend-container {
  margin: 0 auto;
  position: absolute;
  left: 55rem;
  top: 50rem;
}

.splash-info {
  width: 13rem;
  height: auto;
  display: flex;
  margin: 0 auto 2rem auto;
}

.splash-text {
  text-align: right;
  margin: 0 auto;
  font-size: 1.25rem;
}

.splash-legend-tile {
  width: 2.5rem;
  height: 2.5rem;
  background: white;
  border: 1px solid black;
}

.hit-info {
  width: 13rem;
  height: auto;
  display: flex;
  margin: 0 auto 2rem auto;
}

.hit-text {
  text-align: right;
  margin: 0 auto;
  font-size: 1.25rem;
}

.hit-legend-tile {
  width: 2.5rem;
  height: 2.5rem;
  background: black;
  border: 1px solid white;
}

@media (max-width: 900px) {
  .game-container {
    font-family: 'Black Ops One', cursive;
    color: white;
    display: block;
    margin: 50rem auto 0 auto;
    height: auto;
    width: 100%;
  }
  
  .tile-container { /* Contains both the enemy map and the personal map */
    display: block;
    width: 100%;
    height: auto;
    margin: 0 auto;
  }
  
  .legend-container {
    margin: 0 auto;
    position: static;
    display: block;
    text-align: center;
    width: 100%;
  }
  
  .splash-info {
    width: 13rem;
    height: auto;
    display: flex;
    margin: 0 auto 2rem auto;
  }
  
  .splash-text {
    text-align: right;
    margin: 0 auto;
    font-size: 2.25rem;
  }
  
  .splash-legend-tile {
    width: 3rem;
    height: 3rem;
    background: white;
    border: 1px solid black;
  }
  
  .hit-info {
    width: 13rem;
    height: auto;
    display: flex;
    margin: 0 auto 2rem auto;
  }
  
  .hit-text {
    text-align: right;
    margin: 0 auto;
    font-size: 2.25rem;
  }
  
  .hit-legend-tile {
    width: 3rem;
    height: 3rem;
    background: black;
    border: 1px solid white;
  }  
}

